import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import { Input } from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert';
import cloud from '../img/cloud.png';
import './StyleRegisterMenu.css';

const osName = platform();

var notification = "Войти";
var keyDefault = 0;

const request = new XMLHttpRequest();
var urlRegUsers = "https://13c098f3a487.ngrok.io/regusers2";

async function NotifyIsOff(){
	keyDefault = document.getElementById("input-id").value;
	let response = await fetch(urlRegUsers, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify({key: keyDefault}),  // типа запрашиаемого документа
    	headers: new Headers({
      	'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
  		let date = await response.json();
  		console.log(response.text);
  		if (date.length > 1){
  			console.log("Ok");
  			renderingNotifyIsOff();
  			return false;
  		}
  		else{
  			console.log("Find");
  			return true;
  		}
  		return false;
	} 
	else {
  		console.log(response.status);
  		return false;	
  }
};

function closePopout(){
	notification = null;
}

function renderingNotifyIsOff(){
	notification = "Вы ввели неверный код";
};

class RenderNotify extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    	popout: "Войти"
    };
  }

  componentDidMount() {
	setInterval(() => {
   		this.setState({popout: notification})    
  		}, 100);
  }
  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  render() {
    return (
      <div className = "TextRegisterMenuSignIn">
		{this.state.popout}
      </div>
    );
  }
}

const RegisterMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader>
			<Div className = "PanelHeaderRegisterMenu">
				Регистрация
			</Div>
		</PanelHeader>
		<Div className="MainRegisterMenu">
			<RenderNotify />
			<Div className = "TextRegisterMenuCode">
				Регистрационный код
			</Div>
			<Div>
				<Input id ="input-id" className = "InputRegisterMenu" type="text" placeholder= "Введите регистрационный код">
				</Input>
			</Div>
			<Div className = "ButtonDivRegisterMenu">
				<Button className = "ButtonRegisterMenu" id = "but1" size="xl" mode="outline" onClick={e => props.go(e)} data-to="mainMenu">
					<Div className = "TextButtonRegisterMenu">ВОЙТИ</Div>
				</Button>
			</Div>
		</Div>
	</Panel>
);

class button extends React.Component {
	constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.state = {
    };
  }
  handleClick(e) {
    if(NotifyIsOff()){
    	this.props.go(e);
  	};
  }
  render() {
    return <Button className = "ButtonRegisterMenu" id = "but1" size="xl" mode="outline" onClick={this.handleClick}>
    	<Div className = "TextButtonRegisterMenu">ВОЙТИ</Div>
    </Button>;
  }
}

RegisterMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default RegisterMenu;