import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert';
import './StyleScheduleMenu.css';

const osName = platform();

function showHide(element) {
	if (document.getElementById(element)) { 
    	var obj = document.getElementById(element); 
    	if (obj.style.display != "block") { 
        	obj.style.display = "block"; //Показываем элемент
    	}	
    	else obj.style.display = "none"; //Скрываем элемент
    }
}   


const ScheduleMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader className = "PanelHeaderScheduleMenu" left =
			{<PanelHeaderButton onClick={props.go} data-to="mainMenu"> 
			 {osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			 </PanelHeaderButton>}>
			<Div className = "PanelHeaderScheduleMenu">
				Регистрация
			</Div>
		</PanelHeader>
		<Div className="MainScheduleMenu">
			<Div className = "ButtonBlocksScheduleMenuDay">
				<Button className = "ButtonScheduleMenuDay" size="l" mode="outline">
					<Div className = "TextButtonScheduleMenuDay">ПН</Div>
				</Button>
				<Button className = "ButtonScheduleMenuDay" size="l" mode="outline">
					<Div className = "TextButtonScheduleMenuDay">ВТ</Div>
				</Button>
				<Button className = "ButtonScheduleMenuDay" size="l" mode="outline">
					<Div className = "TextButtonScheduleMenuDay">СР</Div>
				</Button>
				<Button className = "ButtonScheduleMenuDay" size="l" mode="outline">
					<Div className = "TextButtonScheduleMenuDay">ЧТ</Div>
				</Button>
				<Button className = "ButtonScheduleMenuDay" size="l" mode="outline">
					<Div className = "TextButtonScheduleMenuDay">ПТ</Div>
				</Button>
				<Button className = "ButtonScheduleMenuDay" size="l" mode="outline">
					<Div className = "TextButtonScheduleMenuDay">СБ</Div>
				</Button>
				<Button className = "ButtonScheduleMenuDay" size="l" mode="outline">
					<Div className = "TextButtonScheduleMenuDay">ВС</Div>
				</Button>
			</Div>
			<Div className = "TextScheduleMenuFreeTime">
				Свободное время :
			</Div>
			<Div className = "ButtonBlocksScheduleMenuTime">
				<Button className = "ButtonScheduleMenuTime">
					<Div className = "TextButtonScheduleMenuTime">11 : 00</Div>
				</Button>
				<Button className = "ButtonScheduleMenuTime">
					<Div className = "TextButtonScheduleMenuTime">15 : 00</Div>
				</Button>
				<Button className = "ButtonScheduleMenuTime">
					<Div className = "TextButtonScheduleMenuTime">17 : 00</Div>
				</Button>
			</Div>
		</Div>
	</Panel>
);


ScheduleMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default ScheduleMenu;