import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import './StyleMyRecordMenu.css';

const osName = platform();

function showHide(element) {
	if (document.getElementById(element)) { 
    	var obj = document.getElementById(element); 
    	if (obj.style.display != "block") { 
        	obj.style.display = "block"; //Показываем элемент
    	}	
    	else obj.style.display = "none"; //Скрываем элемент
    }
}   

const MyRecordMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader left =
			{<PanelHeaderButton onClick={props.go} data-to="mainMenu"> 
			 {osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			 </PanelHeaderButton>}>
			<Div className = "PanelHeaderMyRecordMenu">
				Запись
			</Div>
		</PanelHeader>
		<Div className="MainMyRecordMenu">
			<Div className = "ButtonBlocksMyRecordMenuDataTime">
				<Button className = "ButtonMyRecordMenuDataTime" size="xl" mode="outline" onClick = {() => showHide("block1")}>
					<Div className = "TextButtonMyRecordMenuDataTime">3 июня</Div>
				</Button>
				<Div className = "HiddenBlock" id = "block1"></Div>
				<Button className = "ButtonMyRecordMenuDataTime" size="xl" mode="outline" onClick = {() => showHide("block2")}>
					<Div className = "TextButtonMyRecordMenuDataTime">4 июня</Div>
				</Button>
				<Div className = "HiddenBlock" id = "block2"></Div>
				<Button className = "ButtonMyRecordMenuDataTime" size="xl" mode="outline" onClick = {() => showHide("block3")}>
					<Div className = "TextButtonMyRecordMenuDataTime">5 июня</Div>
				</Button>
				<Div className = "HiddenBlock" id = "block3"></Div>
				<Button className = "ButtonMyRecordMenuDataTime" size="xl" mode="outline" onClick = {() => showHide("block4")}>
					<Div className = "TextButtonMyRecordMenuDataTime">6 июня</Div>
				</Button>
				<Div className = "HiddenBlock" id = "block4"></Div>
				<Button className = "ButtonMyRecordMenuDataTime" size="xl" mode="outline" onClick = {() => showHide("block5")}>
					<Div className = "TextButtonMyRecordMenuDataTime">7 июня</Div>
				</Button> 
				<Div className = "HiddenBlock" id = "block5"></Div>
			</Div>
		</Div>
	</Panel>
);


MyRecordMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default MyRecordMenu;